import React, { Component, Fragment } from 'react';
import obLogo from './images/icon-openbazaar-text.png';
import './styles/App.css';
import StatsDisplay from './containers/stats-display/stats-display';

// Redux Imports
import { Provider } from 'react-redux';
import { createStore, applyMiddleware } from 'redux';
import reducers from './reducers';
import ReduxPromise from 'redux-promise';
import thunk from 'redux-thunk';
const createStoreWithMiddleware = applyMiddleware(ReduxPromise,thunk)(createStore);

export default class App extends Component {

  getObHeaderFooter() {
    return (
      <Fragment>
        <div>
          <h1 className="fnt-accent3 clr-accent3 neontext1 fnt-large padMd">OpenBazaar</h1>
        </div>
        <div>
          <h2 className="fnt-accent1 neontext6 fnt-large padMd">Stats</h2>
        </div>
      </Fragment>
    );
  }
  render() {
    return (
      <Provider store={createStoreWithMiddleware(reducers)}>
        <div className="ob-stats-app-container clr-bg-bg2 clr-primary1">
          <header className="clr-bg-bg1 ob-header flex row justify-space-between align-center">
            {this.getObHeaderFooter()}
          </header>
          <section className="">
            <StatsDisplay />
          </section>
          <footer className="clr-bg-bg1 flex row justify-space-between align-center">
            {this.getObHeaderFooter()}
          </footer>
        </div>
        </Provider>
    );
  }
}



