import React from "react";
import { PropTypes } from "prop-types";
import './stats-summary-component.css';

const StatsSummaryComponent = ({ totalListings, totalMerchants, totalVendors }) => (
  <div  className="stats-summary flex row justify-space-between align-stretch">
    <p className="padSm clr-accent3">Listings: {totalListings || 'loading...'}</p>
    <p className="padSm clr-accent2">Merchants: {totalMerchants || 'loading...'}</p>
    <p className="padSm clr-accent3">Vendors: {totalVendors || 'loading...'}</p>
  </div>
);

StatsSummaryComponent.propTypes = {
  totalListings: PropTypes.number,
  totalMerchants: PropTypes.number,
  totalVendors: PropTypes.number
};

export default StatsSummaryComponent;



